
.navbar .hamburger {
	 position: relative;
	top: 0px; 
	 width: 50px;
	 display: none;
   cursor: pointer;
}
@media (max-width:767px){
    .navbar .hamburger {
        display: block;
    }
}
.navbar .hamburger span {
	 position: relative;
	 display: block;
	 width: 28px;
	 height: 3px;
   background: black;
   transition: all 0.4s;
}
.navbar .hamburger span:before, .navbar .hamburger span:after {
	 content: '';
	 display: block;
	 height: 3px;
	 left: 0;
	 background: inherit;
	 position: absolute;
	 transition: all 0.4s;
	 -webkit-animation: gradientbgAnim 49s ease infinite;
	 -moz-animation: gradientbgAnim 49s ease infinite;
	 -o-animation: gradientbgAnim 49s ease infinite;
	 animation: gradientbgAnim 49s ease infinite;
}
.navbar .hamburger span:before {
	 top: -10px;
   width: 15px;
}
.navbar .hamburger span:after {
	 bottom: -10px;
   width: 40px;
}
.navbar .hamburger:hover span {
  width: 50px;
}
.navbar .hamburger:hover span:before {
  width: 50px;
}
.navbar .hamburger:hover span:after {
  width: 50px;
}
.navbar .hamburger {
	 transform: translate(0, -50%);
}
.navbar .hamburger.close span {
   width: 0px;
}
.navbar .hamburger.close span:before {
	 top: 0;
   width: 40px;
	 transform: rotate(45deg);
}
.navbar .hamburger.close span:after {
	 bottom: 0;
   width: 40px;
	 transform: rotate(-45deg);
}