*{
    margin: 0px;
    padding: 0%;
    box-sizing: border-box;
}






.header{
    background-image: url('../../images/slider-left-dec.png');
    background-repeat: no-repeat;
    background-size: 60% 100%;
    .inner_header{
        width: 100%;
        max-width: 2000px;
        margin: auto;

    }
    @media (max-width:992px){
        background-image: none;
    }

.home_container{
    width: 90%;
    margin: auto;
    max-height: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    padding: 190px 100px 130px 90px;
    position: relative;
    overflow: hidden;
    flex: 1;
    @media (max-width:992px){
        flex-direction: column;
         padding: 50px 0px 0px 0px;
    }
    @media (max-width:767px){
        width: 100%;
    }
    .home_content{
        flex: 0.5;
        padding: 0px 50px;
        @media (max-width:992px){
       text-align: center;
    }
        h2{
            z-index: 2;
            position: relative;
            font-weight: 700;
            line-height: 70px;
            font-size: 50px;
            margin-bottom: 20px;
        }
        p{
            color: #2a2a2a;
            font-weight: 400;
            margin-bottom: 45px;
            font-size: 15px;
            line-height: 30px;

        }
        .homepage_button{
            width: 100%;
            max-width: 300px;
            display: flex;
            justify-content: space-between;
             @media (max-width:992px){
       text-align: center;
       margin: auto;
    }
            a{
                background-color: #4b8ef1;
                text-transform: capitalize;
                font-size: 15px;
                color: #fff !important;
                display: inline-block !important;
                padding: 10px 20px !important;
                border-radius: 23px;
                font-weight: 500 !important;
                letter-spacing: 0.3px !important;
                transition: all .5s;
                text-decoration: none;
                display: flex;
                align-items: center;
                margin: 0px !important;
                .home_btn_icons{
                    font-size: 1.2rem !important;
                    margin-bottom: -3px;
                }
            }
        }
    }
    .home_image{
        flex: 0.5;
        padding: 30px;
        img{
            width: 100%;
        }
    }
}
}