*{
    padding: 0%;
    margin: 0px;
    box-sizing: border-box;
}
.services{
    width: 100%;
    // background-image: url('../../images/services-left-dec.png');
    background-repeat: no-repeat;
    background-position: left bottom;
    position: relative;
    &::after {
  content: '';
  background-image: url('../../images/services-left-dec.png');
  background-repeat: no-repeat;
  position: absolute;
  left: 0;
  bottom: -300px;
  width: 261px;
  height: 368px;
  z-index: -2;
}
    &::before {
   content: '';
  background-image: url('../../images/services-right-dec.png');
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -999 !important;
  width: 1136px;
  height: 244px;
  z-index: 0;

}
    padding-top: 100px;
    .service_container{
        display: block;
        width: 90%;
        @media (max-width:1100px) {
            width: 94%;
        }
        margin: auto;
        text-align: center;
        .services_content{
            display: block;
            margin: auto;
            margin-bottom: 50px;
            max-width: 1000px;
            h2{
                color: #2a2a2a;
                font-size: 30px;    
                font-weight: 700;
                text-transform: capitalize;
                margin-bottom: 10px;
                .service_span{
                    color: #4b8ef1;
                }
            }
            p{
                margin-top: 10px;
                margin-left: 18%;
                margin-right: 18%;
                font-size: 15px;
                line-height: 30px;
                font-weight: 300;
                color: #afafaf;
                a{
                    color: #4b8ef1;
                    text-decoration: none;
                }
            }
        }


        .services_cards{
            width: 100%;
            max-width: 1300px;
            margin-top: 50px;
            display: flex;
            margin: auto;
            justify-content: space-evenly;
            align-items: center;
            @media (max-width:992px){
                flex-direction: column;

            }
            .service-item{
                position: relative;
               @media (max-width:992px){
                width: 70%;
                height: 100%;
                max-height: 250px;
                margin-top: 30px;

                 }
               @media (max-width:767px){
                width: 100%;
                height: 100%;
                max-height: 350px;
                margin-top: 30px;

                 }
                width: 24%;
                height: 400px;
                z-index: 1;
                padding: 30px;
                box-shadow: 0px 0px 15px rgba(0,0,0,0.1);
                background-color: #fff;
                border-radius: 10px;
                border-top-right-radius: 10px;
                border-top-right-radius: 50px;
                transition: all .3s;
                &:hover{
                    background-image: url('../../images/service-bg.jpg');
                    background-position: right top;
                    background-repeat: no-repeat;
                    background-size: cover;
                                    }
                .icon{
                    margin-bottom: 25px;
                }
                h4{
                    transition: all .3s;
                    font-size: 20px;
                    font-weight: 700;
                    margin-bottom: 15px;
                    &:hover{
                        color: #fff;
                    }
                }
                p{
                    transition: all .3s;
                    font-size: 15px;
                    margin-bottom: 20px;
                    line-height: 30px;
                    font-weight: 300;
                    &:hover{
                        color: #fff;
                    }
                    a{
                        text-decoration: none;
                    }
                }
                .text-button{
                    width: 100%;
                    a{
                        font-size: 14px;
                        text-decoration: none;
                        .arrow_forword{
                            margin-bottom: -5px;
                            margin-left: 2px;
                            font-size: 18px;
                            transition: all 0.3s ease-in-out;
                            &:hover{
                                transform: translateX(15px);
                            }
                        }
                    }
                }
            }
        }

    }
}